import {
  Box,
  Fab,
  Grid,
  List,
  ListItem,
  ListItemButton,
  ListItemIcon,
  ListItemText,
  Typography,
  useMediaQuery,
} from "@mui/material";
import React, { useEffect } from "react";
import Navbar from "../../components/layout/navBar/navbar";

import classes from "./contact.module.css";
import divBg from "../../images/b.jpeg";
import Footer from "../../components/layout/footer/footer";
import Card from "../../components/UI/customCard2/card";

import InstagramIcon from "@mui/icons-material/Instagram";
import WhatsAppIcon from "@mui/icons-material/WhatsApp";
import EmailIcon from "@mui/icons-material/Email";
import PlaceIcon from "@mui/icons-material/Place";
import PhoneIcon from "@mui/icons-material/Phone";
import InfoIcon from "@mui/icons-material/Info";

const message = "Merhaba, randevu almak istiyorum.";
const phone = "9005423822662";
const encodedMessage = encodeURIComponent(message);
const whatsappLink = `https://api.whatsapp.com/send?phone=${phone}&text=${encodedMessage}`;

const handleAddressClick = () => {
  const mapsUrl = "https://goo.gl/maps/UY3pHFWNcW1hfSCh7";
  window.open(mapsUrl, "_blank");
};

const cardContent = (
  <Box>
    <Box>
      <List
        sx={{
          position: "relative",
          opacity: ".8",
          display: "inline-block",
          left: "50%",
          transform: "translateX(-50%)",
          width: "100%",
        }}
      >
        <ListItem disablePadding onClick={handleAddressClick}>
          <ListItemButton>
            <ListItemIcon>
              <PlaceIcon />
            </ListItemIcon>
            <ListItemText primary="Altıntaş Mah. Atatürk Bulvarı No:6 Daire No:112 Nazilli/AYDIN" />
          </ListItemButton>
        </ListItem>
        <ListItem disablePadding>
          <ListItemButton href="tel:05423822662">
            <ListItemIcon>
              <PhoneIcon />
            </ListItemIcon>
            <ListItemText primary="0542 382 26 62" />
          </ListItemButton>
        </ListItem>
        <ListItem disablePadding>
          <ListItemButton href="mailto:busracirak04@gmail.com">
            <ListItemIcon>
              <EmailIcon />
            </ListItemIcon>
            <ListItemText primary="busracirak04@gmail.com" />
          </ListItemButton>
        </ListItem>
      </List>
    </Box>
    <Box
      display="flex"
      justifyContent="center"
      sx={{
        "& > :not(style)": { m: 1 },
        position: "relative",
        color: "white",
      }}
    >
      <Fab
        target="_blank"
        color="secondary"
        aria-label="instagram"
        href="https://instagram.com/psk.danbusra?igshid=YmMyMTA2M2Y="
        size="small"
      >
        <InstagramIcon />
      </Fab>
      <Fab
        href={whatsappLink}
        color="success"
        aria-label="whatsapp"
        target="_blank"
        size="small"
      >
        <WhatsAppIcon />
      </Fab>
      <Fab
        target="_blank"
        href="mailto:busracirak04@gmail.com"
        color="info"
        aria-label="mail"
        size="small"
      >
        <EmailIcon />
      </Fab>
    </Box>
  </Box>
);

const cardContent1 = (
  <>
    <Box display="flex">
      <InfoIcon sx={{ marginX: 1 }} />
      <Typography>
        Nazilli Yaşam Psikolojik Danışma Merkezi'ne yanda verilen iletişim
        bilgileri aracılığı ile ulaşabilirsiniz.
      </Typography>
    </Box>
    <Box display="flex" mt={2}>
      <InfoIcon sx={{ marginX: 1 }} />
      <Typography>
        Adresin üzerine tıkladığınızda Google Haritalar üzerinden yol tarifi
        alabilirsiniz.
      </Typography>
    </Box>
    <Box display="flex" mt={2}>
      <InfoIcon sx={{ marginX: 1 }} />
      <Typography>
        Diğer iletişim kanallarına yönlendirilmek için yanda verilen Intagram,
        WhatsApp veya E-posta simgelerinin üzerine tıklayabilirsiniz.
      </Typography>
    </Box>
    <Box display="flex" mt={2}>
      <InfoIcon sx={{ marginX: 1 }} />
      <Typography>
        Çalışma günleri Pazartesi ve Cumartesi günleri dahil olmak üzere
        arasındaki günler olup, çalışma saatleri 10.00 - 20.00 arasındadır.
      </Typography>
    </Box>
  </>
);
export default function Contact(props) {
  const matches = useMediaQuery("(min-width:768px)");

  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);

  return (
    <>
      <div className={classes.header}>
        <div className={classes.image} />
        {matches ? (
          <>
            <Navbar />
            <Box
              mt={3}
              width="98%"
              marginLeft="1%"
              minHeight="20rem"
              height="auto"
              sx={{
                borderRadius: "2rem",
                backgroundColor: "white",
                justifyContent: "center",
                display: "flex",
                backgroundImage: `url(${divBg})`,
                backgroundRepeat: "no-repeat",
                backgroundSize: "cover",
              }}
              mb={5}
            >
              <Grid container spacing={6} m={0.3} mb={1}>
                <Grid item xs={8} ml={-2}>
                  <Card
                    title="Yardımcı Bilgiler"
                    content={cardContent1}
                    height="88%"
                  />
                </Grid>
                <Grid item xs={4} ml={-2}>
                  <Card
                    title="İletişim Bilgileri"
                    content={cardContent}
                    height="88%"
                  ></Card>
                </Grid>
              </Grid>
            </Box>
          </>
        ) : (
          <>
            <div className={classes.header}>
              <div className={classes.image}>
                <img src={divBg} alt="div background"></img>
              </div>
              <Navbar />
            </div>
            <Box mb={-5}>
              <Grid container spacing={6} mt={1}>
                <Grid item>
                  <Card
                    title="Yardımcı Bilgiler"
                    content={cardContent1}
                    height="95%"
                  />
                </Grid>
                <Grid item>
                  <Card
                    title="İletişim Bilgileri"
                    content={cardContent}
                    height="92%"
                  ></Card>
                </Grid>
              </Grid>
            </Box>
          </>
        )}

        <Footer />
      </div>
    </>
  );
}
